<template>    
	
       <div class="">
    <!-- 選擇地區 -->
        <sidebar></sidebar>

    <!-- 顯示地圖和 UBike 站點 -->
        <mapss></mapss>
    </div>
   
</template>

<script>
import mapss from '@/components/ubike/detail/map';
import sidebar from '@/components/ubike/detail/sidebar';
//Vuex Funciton
import { mapActions } from 'vuex';
export default {
     name: 'ubike_map',
  data () {
    return {
     
           }
  },
  components:{
    mapss,
    sidebar
  },
  methods:{        
         ...mapActions({           
           ajaxData:'ajaxData',
           filterAreaData:'filterAreaData',
         }),  
  },
  computed:{
     
  },
  mounted() {   
    this.ajaxData({city:'臺北市',area:'信義區'});   
  },
}
</script>





