import Vue from 'vue'
import Router from 'vue-router'
import ubike from '@/components/ubike/main'
Vue.use(Router)

export default new Router({
  routes: [  
     {
      path:'/',
      name:'weather',
      component:ubike    
    },      
    {
      path:'/Login',
      name:'Login',
      component:ubike
    },
  ]
})




