<template>
     <div class="col-sm-9">
            <!-- 初始化地圖設定 -->
   
    <!-- 初始化地圖設定 -->
    <l-map
      ref="myMap"
      :zoom="zoom"
      :center="center"
      :options="options"
      style="height: 100vh;">
      <!-- 載入圖資 -->
      <l-tile-layer :url="url" :attribution="attribution" />
      
      <!-- 自己所在位置 -->
     
      <!-- 創建標記點 -->
      <l-marker :lat-lng="item.local" v-for="item in getAreaData" :key="item.id">
        <!-- 彈出視窗 -->
        <l-tooltip>
          <p><strong style="font-size: 20px;">{{item.sna}}</strong></p> 
                      <strong style="font-size: 16px; color: #d45345;">可租借車輛剩餘：{{item.sbi}} 台</strong><br>
                      可停空位剩餘: {{item.bemp}}<br>
                      <small>最後更新時間: {{item.mday}}</small>
        </l-tooltip>
      </l-marker>
    </l-map>

  </div>
</template>

<script>
import { mapGetters } from 'vuex'; 
export default {
  name: "App",
  data() {
    return {
      // 模擬資料
      data: [
      
      ],
      zoom: 15,
      center: [25.0365638889, 121.5686639],
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      options: {
        zoomControl: false
      },
      icon: {
        type: {
          black:
            "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-black.png",
          gold:
            "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-gold.png"
        },
        shadowUrl:
          "https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png",
        iconSize: [25, 41],
        iconAnchor: [12, 41],
        popupAnchor: [1, -34],
        shadowSize: [41, 41]
      }
    };
  },
  mounted() {
    // 等地圖創建後執行

  },
  computed:{
      ...mapGetters(
        {
          getAreaData:'getAreaData',
          getAreaPin:'getAreaPin',
          getSelectCity:'getSelectCity',
        }),
  },
  watch:{
    getAreaData(){
        var vm=this;
        vm.center=vm.getAreaPin;
    },
    getSelectCity(){
      var vm=this;
      switch(vm.getSelectCity)
      {
          case '彰化縣':
          case '苗栗縣':
          case '屏東縣':
          case '金門縣':
            vm.zoom=12;
            break;
          default:
            vm.zoom=15;
            break;
      }
    },
  },
};
</script>

<style>
  html,body {
    padding: 0;
    margin: 0;
  }
</style>



