export default{
    state:{   
      allData:{}, 
      selectCity:'',
      areaPin:[],
      CitiesName_List:[], 
      areaData:[],       
    },
    getters:{ 
        getCititesNames(state){
          return state.CitiesName_List;
        },
        getSelectCity(state){
          return state.selectCity;
        },
        getAreaData(state){
           return state.areaData;
        },
        getAreaPin(state){
           return state.areaPin;
        },
     },
    actions:{       
        ajaxData(context,payload){     
            context.commit('changeLoadingOverlay',true,{root:true});       
            var api = '';        
            switch(payload.city)
            {
                case '臺北市':
                    api = 'https://tcgbusfs.blob.core.windows.net/blobyoubike/YouBikeTP.json';
                    break; 
                case '新北市':
                    api = 'https://script.google.com/macros/s/AKfycbxs3QX-GGwWZiGEloxDiuM81fu6IXidHesQEZaGinQ7YEbMmf0/exec?url=data.ntpc.gov.tw/api/datasets/71CD1490-A2DF-4198-BEF1-318479775E8A/json?size=999';
                    break;  
                case '桃園市':                   
                    api  = 'https://script.google.com/macros/s/AKfycbzBi_Tg3nV3Ym7IvYEl2TVn_Gabxl-kNVhngkE7G7q31uL3J8HRivmfrjNcuMCDQSoukw/exec';
                    break; 
                case '彰化縣':
                    api ='https://script.google.com/macros/s/AKfycbzxme1hYHmtiWFu1IPp7TVOai8xvuMddy8o9cNLa98i_s6wRqHAOBsp_jDkeSiMRfjU/exec';
                    break;
                case '苗栗縣':
                    api = 'https://script.google.com/macros/s/AKfycbz2dMR7qjCOWTrqkLR6mBBy_V2ug_rFtBw6pL5edE5jtOXgMTosN9FPfA9FqAB2kNCc9Q/exec';
                    break;                
                case '臺中市':
                    api = 'https://datacenter.taichung.gov.tw/swagger/OpenData/9af00e84-473a-4f3d-99be-b875d8e86256';
                    break;  
                case '臺南市':
                    api = 'https://script.google.com/macros/s/AKfycbxQhJQCEO6Nb3bSfI95sNP0m_l2qGriaSyHpuNfTJYqp0hsrMGfr_o60PbzR2ozIoLpnw/exec';
                    break;
                case '高雄市':
                    api = 'https://api.kcg.gov.tw/api/service/Get/b4dd9c40-9027-4125-8666-06bef1756092';
                    break;  
                case '屏東縣':
                    api = 'https://script.google.com/macros/s/AKfycbzIIQk74iTCgsKXh6VWXcWJFrtJoyNPN3d8CcDllO3bS6di6ckGM86SqqTi2f9QiT2b/exec';
                    break;
                case '金門縣':
                    api = 'https://script.google.com/macros/s/AKfycbyTHaVPiNZkzQXtpfMKjGZReegxuCxibtiBxeQcRrfGt6yzkmmOROJ17UNgM-b51iLVBA/exec';
                    break;                
            }
           
            fetch(		 
		        api
             )
          .then((response) => {	 
            return response.json();              
          })
          .then((res)=>{  
            //console.log(res);
            context.commit('updateselectcity',payload.city);     
            switch(payload.city)
            {
              case '臺北市':              
              case '臺中市':
                context.commit('areaNameFilter',res.retVal);               
                break;  
              case '新北市':
              case '桃園市':
                context.commit('areaNameFilter',res);  
                break;
              case '臺南市':
                context.commit('areaNameFilter_Tainan',res); 
                break;
              case '高雄市':
                context.commit('areaNameFilter',res.data.retVal);
                break;  
              default:
                context.commit('areaNameFilter_Another',payload.area);
                break;
            }

            switch(payload.city)
            {
              case '臺北市':              
              case '臺中市':             
              case '新北市':
              case '桃園市':
              case '高雄市':
                context.commit('areaDataFilter',payload.area);
                break;
              case '臺南市':
                context.commit('areaDataFilter_Tainan',payload.area);
                break;
              default:
                context.commit('areaDataFilter_Another',res);
                break;
            }           
            context.commit('changeLoadingOverlay',false,{root:true});

          })
          .catch(() => {			 
            alert("api發生錯誤，請再點選一下。");
            context.commit('changeLoadingOverlay',false,{root:true});
          });
        },      
    },
    mutations:{
        updateselectcity(context,payload){
          context.selectCity=payload;
        },
        areaNameFilter(context,payload){            
              context.allData=payload;  
              context.CitiesName_List=[];           
              for (var prop in payload) {               
                if(context.CitiesName_List.indexOf(payload[prop].sarea)==-1){context.CitiesName_List.push(payload[prop].sarea);}
              }// for              
          },
        areaDataFilter(context,payload){           
            var count=0;
            context.areaData=[];
            for (var prop in context.allData) {                      
                if(context.allData[prop].sarea===payload)
                {   count++;
                    context.areaPin=[context.allData[prop].lat,context.allData[prop].lng];
                    context.areaData.push(
                        {
                           id:count,
                           ar:context.allData[prop].ar,
                           sna:context.allData[prop].sna,
                           local:[context.allData[prop].lat,context.allData[prop].lng],
                           tot:context.allData[prop].tot,
                           bemp:context.allData[prop].bemp,
                           sbi:context.allData[prop].sbi,
                           mday:context.allData[prop].mday,
                        }
                    );
                }
              }// for 
              //console.log(context.areaData);  
        },       

        areaNameFilter_Tainan(context,payload){            
          context.allData=payload;  
          context.CitiesName_List=[];           
          for (var prop in payload) {               
            if(context.CitiesName_List.indexOf(payload[prop].District)==-1){context.CitiesName_List.push(payload[prop].District);}
          }// for              
        },
        areaDataFilter_Tainan(context,payload){           
          var count=0;
          context.areaData=[];
          for (var prop in context.allData) {                      
              if(context.allData[prop].District===payload)
              {   count++;
                  context.areaPin=[context.allData[prop].Latitude,context.allData[prop].Longitude];
                  context.areaData.push(
                      {
                         id:count,
                         ar:context.allData[prop].Address,
                         sna:context.allData[prop].StationName,
                         local:[context.allData[prop].Latitude,context.allData[prop].Longitude],
                         tot:context.allData[prop].Capacity,
                         bemp:context.allData[prop].AvaliableSpaceCount,
                         sbi:context.allData[prop].AvaliableBikeCount,
                         mday:context.allData[prop].UpdateTime,
                      }
                  );
              }
            }// for 
            //console.log(context.areaData);  
        },

        areaNameFilter_Another(context,payload){    
          context.CitiesName_List=[];
          context.CitiesName_List.push(payload);     
        },
        areaDataFilter_Another(context,payload){           
          var count=0;
          context.areaData=[];
          //console.log(payload);
          //console.log(payload.city);
          for(var item in payload.city)
          {
              for(var item_sum in payload.sum)
              {
                if(payload.city[item].StationUID==payload.sum[item_sum].StationUID)
                {
                  count++;
                  context.areaPin=[payload.city[item].StationPosition.PositionLat,payload.city[item].StationPosition.PositionLon];
                  context.areaData.push(
                      {
                         id:count,
                         ar:'',
                         sna:payload.city[item].StationName.Zh_tw,
                         local:[payload.city[item].StationPosition.PositionLat,payload.city[item].StationPosition.PositionLon],
                         tot:payload.city[item].BikesCapacity,
                         bemp:payload.sum[item_sum].AvailableReturnBikes,
                         sbi:payload.sum[item_sum].AvailableRentBikes,
                         mday:payload.sum[item_sum].UpdateTime,
                      }
                  );//push
                }// if
              }//payload.sum
          }//payload.city
          console.log(context.areaData);
        },

    },
}


