<template>
     <div class="toolbox col-sm-3 p-2 bg-white">
        <div class="form-group d-flex">
        <label for="city" class="col-form-label mr-2 text-right">縣市</label>
        <div class="flex-fill">
             <select id="city" class="form-control" @change="clickCity($event)">
                <!-- 製作下拉選單 -->
                <option v-for="item in city" :key="item">
                        {{item}}
                </option>
            </select>
        </div>
        </div>
        <div class="form-group d-flex">
        <label for="dist" class="col-form-label mr-2 text-right">地區</label>
        <div class="flex-fill">
            <select id="dist" class="form-control" @change="clickArea($event)" v-model="area">
                <!-- 製作下拉選單 -->
                <option v-for="area in getCityNames" :key="area">
                     {{area}}
                </option>
            </select>
        </div>
        </div>
    </div>
</template>

<script>
//Vuex Funciton
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex'; 
export default {
     name: 'ubike_map',
  data () {
    return {
     city:['臺北市','新北市','桃園市','彰化縣','苗栗縣','臺中市','臺南市','高雄市','屏東縣','金門縣'],  
     area:'信義區', 
           }
  },  
  methods:{        
         ...mapActions({           
           ajaxData:'ajaxData',
         }),  
         clickArea(event){
             var vm=this;            
             this.ajaxData({city:vm.getSelectCity,area:event.target.value});  
         },
         clickCity(event){ 
           var vm=this;        
           switch(event.target.value)
           {
              case '臺北市':
                  this.ajaxData({city:'臺北市',area:'信義區'});
                  vm.area='信義區';
                break;
              case '新北市':
                  this.ajaxData({city:'新北市',area:'新店區'});
                  vm.area='新店區';
                break;
              case '桃園市':
                  this.ajaxData({city:'桃園市',area:'桃園區'});
                  vm.area='桃園區';
                break;
              case '彰化縣':
                  this.ajaxData({city:'彰化縣',area:'彰化'});
                  vm.area='彰化';
                  break;
              case '苗栗縣':
                  this.ajaxData({city:'苗栗縣',area:'苗栗'});
                  vm.area='苗栗';
                  break;
              case '臺中市':
                  this.ajaxData({city:'臺中市',area:'南區'});
                  vm.area='南區';
                break;
              case '臺南市':
                  this.ajaxData({city:'臺南市',area:'仁德區'});
                  vm.area='仁德區';
                break;
              case '高雄市':
                  this.ajaxData({city:'高雄市',area:'左營區'});
                  vm.area='左營區';
                  break;
              case '屏東縣':
                  this.ajaxData({city:'屏東縣',area:'屏東'});
                  vm.area='屏東';
                  break;
              case '金門縣':
                  this.ajaxData({city:'金門縣',area:'金門'});
                  vm.area='金門';
                  break;              
           }
         },
  },
  computed:{
      ...mapGetters(
        {
          getCityNames:'getCititesNames',
          getSelectCity:'getSelectCity',
        }),
  },
}
</script>

<style scoped lang="scss">
    @import 'bootstrap/dist/css/bootstrap.min.css';
    #map {
        position: relative;
        height: 100vh;
    }
</style>


